"use client";

import { Permission, Role } from "@/interface/types";
import apiClient from "@/lib/AxiosUtils";
import React, { useEffect, useState } from "react";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Checkbox } from "@/components/ui/checkbox";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";

const RolePermission = () => {
  const [roles, setRoles] = useState<Role[]>([]);
  const [allPermissions, setAllPermissions] = useState<Permission[]>([]);
  const [selectedRoleId, setSelectedRoleId] = useState<number | null>(null);

  const getAllPermissions = async () => {
    try {
      const res = await apiClient.get(`/permissions/view`);
      setAllPermissions(res.data.result as Permission[]);
    } catch (error) {
      alert("Error fetching permissions");
    }
  };

  const getAllRoles = async () => {
    try {
      const res = await apiClient.get(`/permissions/view-all-roles`);
      const rolesData = res.data.result as Role[];
      setRoles(rolesData);
      if (rolesData.length > 0) {
        setSelectedRoleId(rolesData[0].roleId);
      }
    } catch (error) {
      alert("Error fetching roles");
    }
  };

  useEffect(() => {
    getAllPermissions();
    getAllRoles();
  }, []);

  const selectedRole = roles.find((r) => r.roleId === selectedRoleId);

  const isPermissionChecked = (permissionId: number): boolean => {
    return (
      selectedRole?.permissions?.some((p) => p.id === permissionId) ?? false
    );
  };

  return (
    <div className="max-w-3xl mx-auto py-10">
      <Card>
        <CardHeader>
          <CardTitle>Manage Role Permissions</CardTitle>
        </CardHeader>
        <CardContent className="space-y-6">
          {/* Role Dropdown */}
          <div className="space-y-1">
            <Label>Select Role</Label>
            <Select
              value={selectedRoleId?.toString()}
              onValueChange={(value) => setSelectedRoleId(Number(value))}
            >
              <SelectTrigger className="w-full">
                <SelectValue placeholder="Select a role" />
              </SelectTrigger>
              <SelectContent>
                {roles.map((role) => (
                  <SelectItem key={role.roleId} value={role.roleId.toString()}>
                    {role.roleTitle}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          {/* Permissions List */}
          <div>
            <Label className="text-base">Permissions</Label>
            <div className="grid grid-cols-1 sm:grid-cols-2 gap-3 mt-3">
              {allPermissions.map((permission) => (
                <div
                  key={permission.id}
                  className="flex items-center space-x-2 border p-2 rounded-md"
                >
                  <Checkbox
                    checked={isPermissionChecked(permission.id)}
                    disabled
                  />
                  <div className="text-sm">
                    <div className="font-medium">
                      {permission.permissionName}
                    </div>
                    <div className="text-muted-foreground">
                      {permission.description}
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default RolePermission;
